<script>
import ContentHeader from "@/index/shared/ContentHeader.vue";
import { dataTableService } from "@/common/datatable-mixin.js";
import ZohoSearch from "@/components/ZohoSearch.vue";
//import PeriodPicker from "@/components/PeriodPicker.vue";
import PrintHeader from "@/components/PrintHeader.vue";
import BootstrapModal from "@/components/BootstrapModal.vue";
export default {
  name: "OrdersReport",
  mixins: [dataTableService],
  components: { ContentHeader, ZohoSearch, PrintHeader, BootstrapModal },
  data: () => ({
    fetchUrl: "reports/orders",
    comment: null,
    deletedOrder: null,
  }),
  methods: {
    handleDestroy() {
      this.$http
        .post(
          `orders/delete/`,
          this.$helper.generateFormData({
            order_id: this.deletedOrder.id,
            comment: this.comment,
          })
        )
        .then((response) => {
          if (response.data.status) {
            let index = this.rows.findIndex(
              (item) => item.id == this.deletedOrder.id
            );
            this.rows.splice(index, 1);
            this.deletedOrder = null;
            this.comment = null;
            this.toggleModal();
            this.$store.commit("SET_FLASH_MESSAGE", {
              type: "success",
              message: "Sales deleted successfully",
            });
          }
        });
    },
  },
  computed: {
    grandTotal() {
      return this.rows.reduce((a, b) => a + Number(b.grand_total), 0);
    },
  },
};
</script>
<template>
  <content-header
    title="Orders Report"
    subtitle="Explore orders report and it details throughout period of time"
  ></content-header>
  <div class="content__boxed">
    <div class="content__wrap">
      <div class="card">
        <div class="card-header">
          <div class="row">
            <div class="col-md-8 d-flex align-items-center mb-1">
              <div
                class="date-range d-flex align-items-center"
                style="width: 200px"
              >
                <div class="d-flex align-items-center input-wrapper">
                  <input
                    type="date"
                    v-model="newFilter.from"
                    class="form-control"
                    @click="displayPicker"
                  />
                  <span class="input-separator">-</span>
                  <input
                    type="date"
                    v-model="newFilter.to"
                    class="form-control"
                    @click="displayPicker"
                  />
                </div>
                <div class="icon ms-auto">
                  <i class="demo-pli-calendar-4 fs-5"></i>
                </div>
              </div>
              <div class="ms-1">
                <zoho-search
                  url="users/search?role=waiters"
                  label="name"
                  primary-key="id"
                  placeholder="select a waiter"
                  v-model="newFilter.waiter"
                  :load-defaults="true"
                  fetch-flag="SEARCHING_FOR_WAITERS"
                ></zoho-search>
              </div>
            </div>
            <div
              class="col-md-4 d-flex gap-1 align-items-center justify-content-md-end mb-1"
            >
              <button class="btn btn-primary" @click="handlePaginationFilter">
                <span>Apply</span>
              </button>
              <button
                class="btn btn-icon btn-outline-primary"
                @click="resetFilters"
              >
                <i class="demo-pli-refresh fs-5"></i>
              </button>
              <button
                class="btn btn-icon btn-outline-light"
                @click="handlePrint('#PaginationTable')"
              >
                <i class="demo-pli-printer fs-5"></i>
              </button>
              <button
                class="btn btn-icon btn-outline-light"
                @click="handleExcelExport('PaginationTable')"
              >
                <i class="demo-pli-file-excel fs-5"></i>
              </button>
            </div>
          </div>
        </div>
        <div class="card-body">
          <div class="table-responsive" id="PaginationTable">
            <print-header>
              <template #title>
                <div class="text-center mb-1">
                  <h5 class="text-center">Orders Report</h5>
                </div>
              </template>
            </print-header>
            <table class="table table-striped table-sm">
              <thead>
                <tr>
                  <th>Order #</th>
                  <th>Table</th>
                  <th>Waiter</th>
                  <th>Branch</th>
                  <th>Amount</th>
                  <th>Invoiced</th>
                  <th>Status</th>
                  <th>Created at</th>
                  <th class="action"></th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="order in rows" :key="order.reference">
                  <td>
                    <span>#{{ $helper.generateVoucherNo(order.id) }}</span>
                  </td>
                  <td>
                    <a class="btn-link" href="javascript:void(0)">{{
                      order?.table?.name
                    }}</a>
                  </td>
                  <td>
                    <a class="btn-link" href="javascript:void(0)">
                      <span class="fw-bolder">
                        {{ order.waiter ? order.waiter.name : "Admin" }}</span
                      >
                    </a>
                  </td>
                  <td>
                    <a class="btn-link" href="javascript:void(0)">
                      {{ order.branch ? order.branch.name : "Headquarter" }}</a
                    >
                  </td>
                  <td>
                    <span class="fw-bolder">{{
                      formatMoney(order.grand_total)
                    }}</span>
                  </td>
                  <td>
                    <span
                      class="fw-bolder text-success"
                      v-if="order.printed == 1"
                      >YES</span
                    >
                    <span class="fw-bolder text-warning" v-else>NO</span>
                  </td>
                  <td class="fs-5">
                    <span
                      class="d-block badge bg-info"
                      v-if="order.resolved == 1"
                      >Delivered</span
                    >
                    <span class="d-block badge bg-warning" v-else>Pending</span>
                  </td>
                  <td>
                    <span>{{
                      `${$helper.formatDate(
                        order.created_at
                      )} ${$helper.formatOrderTime(order.created_at)}`
                    }}</span>
                  </td>
                  <td class="text-end text-nowrap action">
                    <div class="">
                      <button
                        class="btn btn-icon btn-xs btn-light"
                        data-bs-toggle="dropdown"
                        aria-expanded="false"
                      >
                        <i class="demo-pli-dot-horizontal"></i>
                        <span class="visually-hidden">Toggle Dropdown</span>
                      </button>
                      <ul class="dropdown-menu dropdown-menu-end" style="">
                        <li>
                          <a class="dropdown-item" href="javascript:void(0)"
                            ><svg
                              height="16px"
                              xmlns="http://www.w3.org/2000/svg"
                              viewBox="0 0 24 24"
                              fill="none"
                              stroke="currentColor"
                              stroke-width="2"
                              stroke-linecap="round"
                              stroke-linejoin="round"
                              class="me-1"
                            >
                              <path
                                d="M1 12s4-8 11-8 11 8 11 8-4 8-11 8-11-8-11-8z"
                              ></path>
                              <circle cx="12" cy="12" r="3"></circle></svg>View Items</a
                          >
                        </li>
                        <!-- <li>
                          <a class="dropdown-item" href="javascript:void(0)"
                            ><i class="demo-pli-pen-5 fs-5 me-1"></i>Edit</a
                          >
                        </li> -->
                        <li>
                          <a
                            class="dropdown-item"
                            href="javascript:void(0)"
                            @click.prevent="
                              () => {
                                deletedOrder = order;
                                toggleModal();
                              }
                            "
                            ><i class="demo-pli-trash fs-5 me-1"></i>Delete</a
                          >
                        </li>
                      </ul>
                    </div>
                  </td>
                </tr>
                <tr>
                  <td></td>
                  <td></td>
                  <td></td>
                  <td></td>
                  <td>
                    <span class="fw-bolder">{{ formatMoney(grandTotal) }}</span>
                  </td>
                  <td></td>
                  <td></td>
                  <td></td>
                  <td></td>
                </tr>
              </tbody>
            </table>
          </div>
          <pagination
            :from="recordsFrom"
            :to="recordsTo"
            :current_page="currentPage"
            :row_count_page="rowCountPage"
            @page-update="handlePagination"
            :total_records="totalRows"
            :page_range="pageRange"
          ></pagination>
        </div>
      </div>
    </div>
  </div>

  <bootstrap-modal
    v-if="modalOpen"
    @close="
      () => {
        deletedOrder = null;
        toggleModal();
      }
    "
    modal-size="modal-default"
  >
    <template #head>
      <span>Delete Order #{{ $helper.padNumber(deletedOrder.id) }}</span>
    </template>
    <template #body>
      <div class="alert alert-warning" role="alert">
        If you confirm, there is way to restore deleted Order.
      </div>
      <div class="row mb-2">
        <label class="col-lg-3 col-md-5 fw-bold required">Comment</label>
        <div class="col-lg-9 col-md-7">
          <textarea
            v-model="comment"
            rows="3"
            class="form-control"
            placeholder="Optional comment"
          ></textarea>
        </div>
      </div>
      <div class="row">
        <label class="col-lg-3 col-md-5"></label>
        <div class="col-lg-6 col-md-7">
          <wolf-button
            class="bnt-block btn btn-danger"
            activator="REJECTING_ORDER"
            :disabler="!comment"
            @clicked="handleDestroy"
          >
            Confirm
          </wolf-button>
        </div>
      </div>
    </template>
  </bootstrap-modal>
</template>
