<script>
import ContentHeader from "@/index/shared/ContentHeader.vue";
import { reports } from "@/index/shared/reports.js";
export default {
  name: "ReportsIndex",
  components: { ContentHeader },
  data: () => reports,
  computed: {
    isReady() {
      return this.loggedUser && Object.keys(this.loggedUser).length > 0;
    },
    permissions() {
      return (
        this.isReady &&
        (this.loggedUser.role.pos_permissions ||
          this.loggedUser.role.permissions)
      );
    },
  },
  methods: {
    canView(report) {
      if (this.userRole == "ADMINISTRATOR" || !this.loggedUser.role_id) {
        return true;
      }
      const _report = report.toLowerCase().replaceAll(" ", "_");
      const permissions = this.permissions.back_office;
      return (
        permissions.reports[_report] &&
        permissions.reports[_report].includes("R")
      );
    },
    canAccesssReports(reports) {
      if (this.userRole == "ADMINISTRATOR" || !this.loggedUser.role_id) {
        return true;
      }
      const permissions = this.permissions.back_office;
      return reports.some((report) => {
        const _report = report.name.toLowerCase().replaceAll(" ", "_");
        return (
          permissions.reports[_report] &&
          permissions.reports[_report].includes("R")
        );
      });
    },
  },
};
</script>
