<script>
import VueApexCharts from "vue3-apexcharts";
import Avatar from "@/components/Avatar.vue";
import PeriodPicker from "@/components/PeriodPicker.vue";
export default {
  name: "WaitersPerformance",
  components: { apexchart: VueApexCharts, Avatar, PeriodPicker },
  data: () => ({
    rows: [],
  }),
  beforeMount() {
    this.getWaitersData();
  },
  computed: {
    waiters() {
      const rows = [...this.rows];
      return rows.sort((a, b) => b.earnings - a.earnings);
    },
    chatSeries() {
      return [
        {
          name: "Earnings",
          type: "bar",
          data: this.rows.map((row) => row.earnings),
        },
        {
          name: "Balance",
          type: "line",
          data: this.rows.map((row) => row.balance),
        },
      ];
    },
    chatOptions() {
      return {
        chart: { height: 300, type: "bar", toolbar: { show: !1 } },
        plotOptions: { bar: { borderRadius: 10, columnWidth: "27%" } },
        dataLabels: { enabled: !1 },
        stroke: { width: [2, 4] },
        markers: { size: 4 },
        colors: ["#663b6c", "#4ab0c1"],
        yaxis: {
          labels: {
            style: {
              colors: "#000",
              fontSize: "12px",
              fontWeight: 500,
            },
            formatter: (value) => {
              var result = value;
              if (value >= 1000000000 || value <= -1000000000) {
                result = value / 1e9 + "B";
              } else if (value >= 1000000 || value <= -1000000) {
                result = value / 1e6 + "M";
              } else if (value >= 1000 || value <= -1000) {
                result = value / 1e3 + "K";
              }
              return `${this.appSettings.currency} ${result}`;
            },
          },
        },
        xaxis: {
          labels: { rotate: -45 },
          categories: this.rows.map((row) => row.first_name),
          tickPlacement: "on",
        },
        legend: { show: !1 },
        fill: {
          type: "gradient",
          gradient: {
            shade: "light",
            type: "horizontal",
            shadeIntensity: 0.15,
            gradientToColors: void 0,
            inverseColors: !0,
            opacityFrom: 1,
            opacityTo: 0.85,
            stops: [50, 0, 100],
          },
        },
      };
    },
  },
  methods: {
    getWaitersData(dates = []) {
      let url = "reports/waiters-performance";
      if (Array.isArray(dates) && dates.length > 1) {
        url += `?from=${dates[0]}&to=${dates[1]}`;
      }
      this.$http.get(url).then((response) => {
        this.rows = response.data.rows;
      });
    },
  },
};
</script>
<template>
  <div class="content__header content__boxed overlapping">
    <div class="content__wrap">
      <div class="d-md-flex align-items-end">
        <div class="me-auto">
          <h1 class="page-title mb-2">Waiters Performance Report</h1>
          <h2 class="h5">
            View waiters performance report based on earnings and days worked
          </h2>
        </div>
        <div
          class="flex-grow-1 d-flex flex-wrap justify-content-end align-items-center gap-3"
        >
          <div class="w-auto text-nowrap">
            <PeriodPicker period="CURRENT_MONTH" @selected="getWaitersData" />
          </div>
        </div>
      </div>
    </div>
  </div>

  <div class="content__boxed">
    <div class="content__wrap">
      <div class="nk-content py-4">
        <div class="container-fluid">
          <div class="row mb-3">
            <div class="col-xl-12">
              <div class="card">
                <div class="card-body">
                  <div class="card-head">
                    <h4 class="title">Performance Chart</h4>
                  </div>
                  <apexchart
                    type="bar"
                    height="300"
                    :options="chatOptions"
                    :series="chatSeries"
                  ></apexchart>
                </div>
              </div>
            </div>
          </div>
          <div class="row mb-3">
            <div class="col-xl-12">
              <div class="card">
                <div class="card-body">
                  <div class="card-head">
                    <h4 class="title">Details about waiters performance</h4>
                  </div>
                  <div class="table-responsive">
                    <table class="table table-striped table-sm">
                      <thead>
                        <tr>
                          <th>$/N</th>
                          <th>Waiter Name</th>
                          <th class="text-center">Days Worked</th>
                          <th class="text-center">Tot. Orders</th>
                          <th class="text-center">Tot. Earnings</th>
                          <th></th>
                        </tr>
                      </thead>
                      <tbody>
                        <tr v-for="(waiter, i) in waiters" :key="waiter.id">
                          <td>
                            <span>{{ i + 1 }}</span>
                          </td>
                          <td>
                            <div class="d-flex align-items-center">
                              <avatar
                                :name="waiter.name"
                                width="2.25rem"
                                height="2.25rem"
                              ></avatar>
                              <span class="h5 mb-0 ms-2">
                                {{ waiter.name }}</span
                              >
                            </div>
                          </td>
                          <td class="text-center">
                            <span class="fw-bolder">{{
                              waiter.work_days
                            }}</span>
                          </td>

                          <td class="text-center">
                            <span class="fw-bolder">{{
                              $helper.formatNumber(waiter.orders)
                            }}</span>
                          </td>
                          <td class="text-center">
                            <span class="fw-bolder">{{
                              formatMoney(waiter.earnings)
                            }}</span>
                          </td>
                        </tr>
                        <td></td>
                      </tbody>
                    </table>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
