<script>
import Avatar from "@/components/Avatar.vue";
import PeriodPicker from "@/components/PeriodPicker.vue";
export default {
  name: "CashiersReport",
  components: { Avatar, PeriodPicker },
  data: () => ({
    rows: [],
    paymentModes: [],
  }),
  created() {
    this.getCashiersData();
    this.$http.get("shared/used-payment-modes").then((response) => {
      this.paymentModes = response.data.rows;
    });
  },
  methods: {
    getCashiersData(dates = []) {
      let url = "reports/cashiers";
      if (Array.isArray(dates) && dates.length > 1) {
        url += `?from=${dates[0]}&to=${dates[1]}`;
      }
      this.$http.get(url).then((response) => {
        this.rows = response.data.rows;
      });
    },
  },
};
</script>
<template>
  <div class="content__header content__boxed overlapping">
    <div class="content__wrap">
      <div class="d-md-flex align-items-end">
        <div class="me-auto">
          <h1 class="page-title mb-2">Cashiers Report</h1>
          <h2 class="h5">View cashiers report based on amount received</h2>
        </div>
        <div
          class="flex-grow-1 d-flex flex-wrap justify-content-end align-items-center gap-3"
        >
          <div class="w-auto text-nowrap">
            <PeriodPicker @selected="getCashiersData" />
          </div>
        </div>
      </div>
    </div>
  </div>

  <div class="content__boxed">
    <div class="content__wrap">
      <div class="nk-content py-4">
        <div class="container-fluid">
          <div class="row mb-3">
            <div class="col-xl-12">
              <div class="card">
                <div class="card-body">
                  <div class="table-responsive">
                    <table class="table table-striped table-sm">
                      <thead>
                        <tr>
                          <th>$/N</th>
                          <th>Cashier Name</th>
                          <th>Tot. Sales</th>
                          <th>Tot. Amount</th>
                          <th
                            v-for="mode in paymentModes"
                            :key="'mode' + mode.payment_type"
                          >
                            {{ mode.name }}
                          </th>
                          <th></th>
                        </tr>
                      </thead>
                      <tbody>
                        <tr v-for="(row, i) in rows" :key="row.id">
                          <td>
                            <span>{{ i + 1 }}</span>
                          </td>
                          <td>
                            <div class="d-flex align-items-center">
                              <avatar
                                :name="row.name"
                                width="2.25rem"
                                height="2.25rem"
                              ></avatar>
                              <span class="h5 mb-0 ms-2"> {{ row.name }}</span>
                            </div>
                          </td>
                          <td>
                            <span class="fw-bolder">{{
                              $helper.formatNumber(row.total_sales)
                            }}</span>
                          </td>

                          <td>
                            <span class="fw-bolder">{{
                              formatMoney(row.total_amount)
                            }}</span>
                          </td>
                          <td
                            v-for="item in row?.payments"
                            :key="i + 'payment' + item.id"
                          >
                            <span class="fw-bolder">{{
                              formatMoney(item.amount)
                            }}</span>
                          </td>
                        </tr>
                        <td></td>
                      </tbody>
                    </table>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
